  import React from 'react';

import { storiesOf } from '@storybook/react';

import Element from 'react-bulma-components/lib/components/element';
import { boolean } from '@storybook/addon-knobs';

storiesOf('Element', module).addDecorator(withStorySource(__STORY__, __ADDS_MAP__))
  .add('Default', () => (
    <Element
      paddingless={boolean('paddingless', false)}
      responsive={{
        mobile: {
          display: 'block',
        },
        tablet: {
          display: 'flex',
        },
        desktop: {
          display: 'inline-flex',
          only: true,
        },
        widescreen: {
          display: 'inline-block',
        },
      }}
      hide={{
        tablet: {
          hide: true,
          only: true,
        },
        widescreen: {
          hide: true,
        },
      }}
    >
      Custom Element to use Bulma helper
    </Element>
  ));

  